body {
    background-color: rgb(242,242,242);
  }


#name {
    color: rgb(49,49,49);
    /* text-shadow: 2px 2px 4px rgb(214, 200, 200); */
    margin-top: 20%;
    text-align: center;
    line-height: 90px;  
    font-size: 50px;
}

#dev {
    color: rgb(49,49,49);
    text-align: center;
    margin-top: 40px;
}

.buttons {
    border: 2px solid rgb(49,49,49);
    background: none;
    padding: 5px 15px;
    margin: 35px 25px;
    font-size: 24px;
    color: rgb(49,49,49);
    border-radius: 5px;
}

.buttons:hover {
    background: rgb(49,49,49);
    color:rgb(242,242,242);
}
.contact {
    text-align: center;
    margin-top: 50px;
}

.middle {
    background-color:#ffffff;
    max-width: 90%;
    margin: auto;
    padding: 20px;
    border: 1.5px solid;
    border-radius: 8px;
}


.image-project {
    max-width:400px;
    max-height:300px;
    display:block;
    margin-left:auto;
    margin-right:auto; 
    border: 5px solid white;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
}
.image-project:hover {
    -webkit-transform: translate(-3px, -3px);
    transform: translate(-3px, -3px);
}
